import React, { useState, useEffect } from 'react';
import { 
  Play, Save, Plus, Search, ChevronRight, ChevronDown, 
  Clock, Shield, Database, MoreHorizontal, Code, 
  Settings, Layers, Zap, Trash2, Copy, FileText,
  Box, Hash, ShieldCheck, Globe, CheckCircle2, XCircle,
  Layout, AlignLeft, Braces, Download, Cpu, Activity
} from 'lucide-react';

export const AppPreview: React.FC = () => {
  const [url, setUrl] = useState('https://jsonplaceholder.typicode.com/users');
  const [method, setMethod] = useState('GET');
  const [isLoading, setIsLoading] = useState(false);
  const [activeRequestTab, setActiveRequestTab] = useState('Params');
  const [activeResponseTab, setActiveResponseTab] = useState('Body');
  const [responseVisible, setResponseVisible] = useState(false);
  const [isMethodOpen, setIsMethodOpen] = useState(false);
  
  // Response State
  const [responseStatus, setResponseStatus] = useState<number | null>(null);
  const [responseTime, setResponseTime] = useState<string | null>(null);
  const [responseSize, setResponseSize] = useState<string | null>(null);
  const [responseBody, setResponseBody] = useState<any>(null);
  
  // Performance Stats Simulation
  const [memoryUsage, setMemoryUsage] = useState(45.2);
  const [cpuUsage, setCpuUsage] = useState(0.1);
  const [threads, setThreads] = useState(4);

  useEffect(() => {
    const interval = setInterval(() => {
      setMemoryUsage(prev => +(prev + (Math.random() * 0.4 - 0.2)).toFixed(1));
      setCpuUsage(prev => +(Math.max(0, prev + (Math.random() * 0.2 - 0.1))).toFixed(1));
    }, 2000);
    return () => clearInterval(interval);
  }, []);

  const handleSend = async (overrideMethod?: any, overrideUrl?: string) => {
    const methodToUse = typeof overrideMethod === 'string' ? overrideMethod : method;
    const urlToUse = typeof overrideUrl === 'string' ? overrideUrl : url;

    setIsLoading(true);
    setResponseVisible(false);
    setResponseStatus(null);
    setResponseBody(null);
    
    const startTime = performance.now();
    
    // Simulate CPU/Memory spike on request start
    setCpuUsage(prev => +(prev + 2.5).toFixed(1));
    setThreads(prev => prev + 2);

    try {
      const res = await fetch(urlToUse, { method: methodToUse });
      const endTime = performance.now();
      const duration = (endTime - startTime).toFixed(0);
      
      const blob = await res.blob();
      const size = (blob.size / 1024).toFixed(2);
      
      const text = await blob.text();
      let data;
      try {
        data = JSON.parse(text);
      } catch {
        data = text;
      }
      
      setResponseStatus(res.status);
      setResponseTime(`${duration} ms`);
      setResponseSize(`${size} KB`);
      setResponseBody(data);
    } catch (error) {
      console.error(error);
      setResponseStatus(0);
      setResponseTime('0 ms');
      setResponseSize('0 KB');
      setResponseBody({ error: 'Network Error', details: String(error) });
    } finally {
      setIsLoading(false);
      setResponseVisible(true);
      // Simulate CPU/Memory drop
      setCpuUsage(prev => +(Math.max(0.1, prev - 2.5)).toFixed(1));
      setThreads(prev => Math.max(4, prev - 2));
    }
  };

  const loadPreset = (newMethod: string, newUrl: string) => {
    setMethod(newMethod);
    setUrl(newUrl);
    handleSend(newMethod, newUrl);
  };

  const methods = ['GET', 'POST', 'PUT', 'DELETE', 'PATCH'];

  const getMethodColor = (m: string) => {
    switch(m) {
      case 'GET': return 'text-green-500';
      case 'POST': return 'text-orange-500';
      case 'PUT': return 'text-blue-500';
      case 'DELETE': return 'text-red-500';
      default: return 'text-gray-500';
    }
  };

  return (
    <div className="relative w-full max-w-6xl mx-auto perspective-1000">
      {/* Glow Effect */}
      <div className="absolute -inset-1 bg-gradient-to-r from-orange-500/20 to-rose-500/20 rounded-xl blur-xl opacity-40 animate-pulse-slow"></div>
      
      {/* Window Frame */}
      <div className="relative bg-white dark:bg-[#181818] border border-gray-200 dark:border-white/10 rounded-xl shadow-2xl overflow-hidden flex flex-col h-[800px] font-sans text-[13px]">
        {/* macOS Title Bar */}
        <div className="h-10 bg-gray-100 dark:bg-[#181818] border-b border-gray-200 dark:border-black flex items-center px-4 space-x-4 shrink-0 select-none">
          <div className="flex space-x-2 group">
            <div className="w-3 h-3 rounded-full bg-[#FF5F56] group-hover:flex items-center justify-center text-[8px] text-black/50 font-bold"></div>
            <div className="w-3 h-3 rounded-full bg-[#FFBD2E] group-hover:flex items-center justify-center text-[8px] text-black/50 font-bold"></div>
            <div className="w-3 h-3 rounded-full bg-[#27C93F] group-hover:flex items-center justify-center text-[8px] text-black/50 font-bold"></div>
          </div>
          <div className="flex-1 flex justify-center">
            <span className="text-xs text-gray-500 font-medium flex items-center gap-2 opacity-50">
              <Database className="w-3 h-3" /> Aether Core
            </span>
          </div>
          <div className="w-14"></div>
        </div>

        <div className="flex-1 flex overflow-hidden">
          {/* Sidebar */}
          <div className="w-64 bg-gray-50 dark:bg-[#181818] border-r border-gray-200 dark:border-black flex flex-col shrink-0">
            {/* Sidebar Header */}
            <div className="p-3 space-y-3">
              <div className="flex items-center text-gray-600 dark:text-gray-400 space-x-2 px-1">
                <span className="font-medium hover:text-gray-900 dark:hover:text-white cursor-pointer transition-colors">Repos</span>
                <span className="text-xs opacity-50">main</span>
                <div className="flex-1"></div>
                <div className="flex space-x-1">
                  <div className="p-1 hover:bg-gray-200 dark:hover:bg-white/10 rounded cursor-pointer text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white transition-colors">
                     <Database className="w-3.5 h-3.5" />
                  </div>
                  <div className="p-1 hover:bg-gray-200 dark:hover:bg-white/10 rounded cursor-pointer text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white transition-colors">
                     <Plus className="w-3.5 h-3.5" />
                  </div>
                </div>
              </div>
              <div className="bg-white dark:bg-[#202020] rounded-md flex items-center px-2 py-1.5 border border-gray-200 dark:border-white/5 group focus-within:border-blue-500 dark:focus-within:border-white/20 transition-colors">
                <Search className="w-3.5 h-3.5 text-gray-400 dark:text-gray-500 mr-2 group-focus-within:text-gray-600 dark:group-focus-within:text-white transition-colors" />
                <input 
                  type="text" 
                  placeholder="Search collections..." 
                  className="bg-transparent border-none outline-none text-xs text-gray-700 dark:text-gray-300 w-full placeholder-gray-400 dark:placeholder-gray-500"
                />
              </div>
            </div>

            {/* Sidebar Nav */}
            <div className="flex-1 overflow-y-auto select-none custom-scrollbar">
              <div className="mt-2">
                <SidebarItem icon={<Box className="w-3.5 h-3.5 text-gray-400" />} label="Organ" hasChildren />
                <div className="ml-4 border-l border-gray-200 dark:border-white/10 pl-2 mt-1 space-y-0.5">
                  <SidebarItem icon={<div className="w-3.5 h-3.5 flex items-center justify-center"><div className="w-1.5 h-1.5 rounded-sm border border-gray-500"></div></div>} label="abeleatadwe" active />
                  <div className="ml-4 border-l border-white/10 pl-2 mt-1">
                     <div 
                        onClick={() => loadPreset('GET', 'https://api.ipify.org?format=json')}
                        className="flex items-center py-1 px-2 hover:bg-gray-200 dark:hover:bg-white/5 rounded cursor-pointer text-gray-600 dark:text-gray-300 group justify-between transition-colors"
                     >
                        <div className="flex items-center truncate">
                           <Globe className="w-3.5 h-3.5 mr-2 text-green-500" />
                           <span className="truncate">ipify - Public IP</span>
                        </div>
                        <span className="text-[10px] text-green-500 bg-green-500/10 px-1 rounded hidden group-hover:block">GET</span>
                     </div>
                     <div 
                        onClick={() => loadPreset('POST', 'https://jsonplaceholder.typicode.com/posts')}
                        className="flex items-center py-1 px-2 hover:bg-gray-200 dark:hover:bg-white/5 rounded cursor-pointer text-gray-600 dark:text-gray-300 group justify-between transition-colors"
                     >
                        <div className="flex items-center truncate">
                           <Box className="w-3.5 h-3.5 mr-2 text-orange-500" />
                           <span className="truncate">Create Post</span>
                        </div>
                        <span className="text-[10px] text-orange-500 bg-orange-500/10 px-1 rounded hidden group-hover:block">POST</span>
                     </div>
                     <SidebarItem icon={<div className="w-3.5 h-3.5 flex items-center justify-center"><div className="w-3 h-2.5 bg-gray-600/50 rounded-sm"></div></div>} label="rate_limit" hasChildren />
                  </div>
                </div>
              </div>

              <div className="mt-6">
                <div className="flex items-center px-4 py-1 text-xs text-gray-500 font-medium uppercase tracking-wider cursor-pointer hover:text-gray-700 dark:hover:text-gray-300 transition-colors">
                  <ChevronRight className="w-3 h-3 mr-1" /> Activity
                  <span className="ml-auto">74</span>
                </div>
              </div>

              <div className="mt-2">
                 <div className="flex items-center px-4 py-1 text-xs text-gray-700 dark:text-gray-200 font-medium uppercase tracking-wider cursor-pointer">
                  <ChevronDown className="w-3 h-3 mr-1 text-orange-500" /> History
                </div>
                
                <div className="px-4 py-2">
                  <div className="flex space-x-2 text-[10px]">
                    <span className="bg-orange-600 text-white px-2 py-0.5 rounded-full cursor-pointer hover:bg-orange-500 transition-colors">All</span>
                    <span className="hover:bg-gray-200 dark:hover:bg-[#252525] text-gray-500 dark:text-gray-400 px-2 py-0.5 rounded-full flex items-center cursor-pointer transition-colors"><Zap className="w-2 h-2 mr-1"/> Today</span>
                    <span className="hover:bg-gray-200 dark:hover:bg-[#252525] text-gray-500 dark:text-gray-400 px-2 py-0.5 rounded-full cursor-pointer transition-colors">This Week</span>
                  </div>
                </div>

                <div className="space-y-0.5 mt-1">
                  <HistoryEntry method="GET" url="New Request" status="200" time="0 sec" duration="62ms" active onClick={() => loadPreset('GET', 'https://jsonplaceholder.typicode.com/users')} />
                  <HistoryEntry method="POST" url="http://localhost:50051/users" status="404" time="8 hr ago" duration="71ms" onClick={() => loadPreset('POST', 'http://localhost:50051/users')} />
                  <HistoryEntry method="POST" url="http://localhost:50051/TestSe..." status="404" time="8 hr ago" duration="143ms" onClick={() => loadPreset('POST', 'http://localhost:50051/TestService/Run')} />
                  <HistoryEntry method="POST" url="http://localhost:50051/TestSe..." status="200" time="9 hr ago" duration="26ms" onClick={() => loadPreset('POST', 'http://localhost:50051/TestService/Ping')} />
                </div>
              </div>
            </div>
          </div>

          {/* Main Content */}
          <div className="flex-1 flex flex-col bg-white dark:bg-[#131313] min-w-0">
            
            {/* Tabs */}
            <div className="flex items-center bg-gray-100 dark:bg-[#181818] border-b border-gray-200 dark:border-black h-9 select-none">
               <div className="flex items-center px-4 py-2 bg-white dark:bg-[#131313] text-gray-900 dark:text-gray-200 border-r border-gray-200 dark:border-black border-t-2 border-t-orange-500 h-full min-w-[140px]">
                  <span className={`text-white text-[9px] px-1 rounded mr-2 font-bold ${method === 'GET' ? 'bg-green-600' : method === 'POST' ? 'bg-orange-600' : method === 'DELETE' ? 'bg-red-600' : 'bg-blue-600'}`}>{method}</span>
                  <span className="truncate">New Request</span>
                  <span className="ml-auto text-gray-400 dark:text-gray-500 hover:text-gray-900 dark:hover:text-white cursor-pointer pl-2 transition-colors"></span>
               </div>
               <div className="flex items-center px-4 py-2 bg-gray-100 dark:bg-[#181818] text-gray-500 border-r border-gray-200 dark:border-black border-t-2 border-t-transparent h-full hover:bg-gray-200 dark:hover:bg-[#1a1a1a] cursor-pointer transition-colors min-w-[140px]">
                  <span className="bg-green-600/20 text-green-600 text-[9px] px-1 rounded mr-2 font-bold">GET</span>
                  <span className="truncate">New Request</span>
                  <span className="ml-auto hover:text-gray-900 dark:hover:text-white cursor-pointer pl-2 opacity-0 hover:opacity-100 transition-opacity"></span>
               </div>
               <div className="px-3 text-gray-400 dark:text-gray-500 hover:text-gray-900 dark:hover:text-white cursor-pointer transition-colors">
                  <Plus className="w-4 h-4" />
               </div>
            </div>

            {/* Protocol Bar */}
            <div className="flex items-center px-4 py-2 space-x-6 border-b border-gray-200 dark:border-white/5 text-xs text-gray-500 bg-white dark:bg-[#131313]">
               <div className="flex items-center text-gray-900 dark:text-white bg-gray-100 dark:bg-[#252525] border border-gray-200 dark:border-white/10 px-2 py-0.5 rounded cursor-pointer shadow-sm">
                  <Globe className="w-3 h-3 mr-1.5" /> HTTP
               </div>
               <div className="hover:text-gray-900 dark:hover:text-gray-300 cursor-pointer transition-colors">GraphQL</div>
               <div className="hover:text-gray-900 dark:hover:text-gray-300 cursor-pointer flex items-center transition-colors"><Zap className="w-3 h-3 mr-1"/> WebSocket</div>
               <div className="hover:text-gray-900 dark:hover:text-gray-300 cursor-pointer flex items-center transition-colors"><Zap className="w-3 h-3 mr-1"/> SSE</div>
               <div className="hover:text-gray-900 dark:hover:text-gray-300 cursor-pointer flex items-center transition-colors"><Box className="w-3 h-3 mr-1"/> gRPC</div>
               <div className="flex-1"></div>
               <div className="flex items-center space-x-1 hover:text-gray-900 dark:hover:text-white cursor-pointer transition-colors"><Layers className="w-3 h-3" /> <span>Vertical</span></div>
            </div>

            {/* Address Bar */}
            <div className="p-3 border-b border-gray-200 dark:border-white/5 flex gap-2 bg-white dark:bg-[#131313] relative z-20">
               <div className="flex-1 flex items-center bg-gray-50 dark:bg-[#181818] border border-gray-200 dark:border-white/10 rounded hover:border-gray-300 dark:hover:border-white/20 transition-colors group focus-within:border-blue-500/50 relative">
                  <div 
                    className="px-2 py-1 border-r border-gray-200 dark:border-white/10 cursor-pointer hover:bg-gray-200 dark:hover:bg-white/5 transition-colors relative"
                    onClick={() => setIsMethodOpen(!isMethodOpen)}
                  >
                     <span className={`text-xs font-bold flex items-center ${getMethodColor(method)}`}>
                        {method} <ChevronDown className="w-3 h-3 ml-1 opacity-50" />
                     </span>
                     
                     {/* Method Dropdown */}
                     {isMethodOpen && (
                        <div className="absolute top-full left-0 mt-2 w-24 bg-white dark:bg-[#1A1A1A] border border-gray-200 dark:border-white/10 rounded-lg shadow-xl overflow-hidden z-50 animate-in fade-in zoom-in-95 duration-100">
                           {methods.map((m) => (
                              <div 
                                 key={m}
                                 className={`px-3 py-2 text-xs font-bold hover:bg-gray-100 dark:hover:bg-white/5 cursor-pointer ${getMethodColor(m)}`}
                                 onClick={(e) => {
                                    e.stopPropagation();
                                    setMethod(m);
                                    setIsMethodOpen(false);
                                 }}
                              >
                                 {m}
                              </div>
                           ))}
                        </div>
                     )}
                  </div>
                  <div className="px-2 py-1 hidden sm:block">
                     <span className="text-[10px] font-bold text-green-500 bg-green-500/10 px-1.5 py-0.5 rounded">HTTPS</span>
                  </div>
                  <input 
                    type="text" 
                    value={url}
                    onChange={(e) => setUrl(e.target.value)}
                    className="flex-1 bg-transparent text-sm px-2 text-gray-900 dark:text-gray-300 focus:outline-none font-mono py-1.5"
                  />
                  <div className="flex items-center px-2 space-x-2">
                     <CheckCircle2 className="w-3.5 h-3.5 text-green-500" />
                     {url && (
                        <XCircle 
                           className="w-3.5 h-3.5 text-gray-600 hover:text-gray-400 cursor-pointer transition-colors" 
                           onClick={() => setUrl('')}
                        />
                     )}
                  </div>
               </div>
               <button 
                 onClick={handleSend}
                 className="bg-orange-600 hover:bg-orange-500 text-white px-4 rounded flex items-center text-sm font-medium shadow-lg shadow-orange-900/20 active:scale-95 transition-all"
               >
                  {isLoading ? (
                     <div className="w-3.5 h-3.5 mr-1.5 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                  ) : (
                     <Play className="w-3.5 h-3.5 mr-1.5 fill-current" /> 
                  )}
                  Send
               </button>
               <div className="flex items-center space-x-1">
                  <button className="bg-gray-100 dark:bg-[#181818] hover:bg-gray-200 dark:hover:bg-[#252525] text-gray-600 dark:text-gray-300 px-3 py-1.5 rounded flex items-center text-xs border border-gray-200 dark:border-white/5 transition-colors">
                     <Save className="w-3.5 h-3.5 mr-1.5" /> Save
                  </button>
                  <button className="bg-gray-100 dark:bg-[#181818] hover:bg-gray-200 dark:hover:bg-[#252525] text-gray-600 dark:text-gray-300 px-3 py-1.5 rounded flex items-center text-xs border border-gray-200 dark:border-white/5 transition-colors hidden md:flex">
                     <Settings className="w-3 h-3 mr-1.5" /> No Environment <ChevronDown className="w-3 h-3 ml-1" />
                  </button>
                  <button className="bg-gray-100 dark:bg-[#181818] hover:bg-gray-200 dark:hover:bg-[#252525] text-gray-600 dark:text-gray-300 px-3 py-1.5 rounded flex items-center text-xs border border-gray-200 dark:border-white/5 transition-colors hidden lg:flex">
                     <Code className="w-3.5 h-3.5 mr-1.5" /> Code
                  </button>
               </div>
            </div>

            {/* Request Tabs */}
            <div className="px-3 border-b border-gray-200 dark:border-white/5 flex items-center space-x-6 text-xs text-gray-500 h-9 bg-white dark:bg-[#131313] overflow-x-auto no-scrollbar">
               {['Params', 'Headers', 'Body', 'Auth', 'Pre-request', 'Tests'].map((tab) => (
                  <div 
                     key={tab}
                     onClick={() => setActiveRequestTab(tab)}
                     className={`h-full flex items-center px-1 cursor-pointer border-b-2 transition-colors whitespace-nowrap ${
                        activeRequestTab === tab 
                        ? 'text-orange-500 border-orange-500' 
                        : 'border-transparent hover:text-gray-900 dark:hover:text-gray-300'
                     }`}
                  >
                     {tab === 'Params' && <ShieldCheck className="w-3 h-3 mr-1.5" />}
                     {tab === 'Headers' && <Layout className="w-3 h-3 mr-1.5" />}
                     {tab === 'Body' && <FileText className="w-3 h-3 mr-1.5" />}
                     {tab === 'Auth' && <Shield className="w-3 h-3 mr-1.5" />}
                     {tab === 'Pre-request' && <Code className="w-3 h-3 mr-1.5" />}
                     {tab === 'Tests' && <CheckCircle2 className="w-3 h-3 mr-1.5" />}
                     {tab}
                  </div>
               ))}
               <div className="flex-1"></div>
               <div className="text-orange-500/80 bg-orange-500/10 px-2 py-0.5 rounded border border-orange-500/20 cursor-pointer flex items-center whitespace-nowrap hover:bg-orange-500/20 transition-colors">
                  <Zap className="w-3 h-3 mr-1" /> Detect Variables
               </div>
            </div>

            {/* Request Content Area */}
            <div className="flex-1 flex flex-col min-h-0 bg-white dark:bg-[#131313]">
               <div className="h-[250px] border-b border-gray-200 dark:border-black flex flex-col">
                  {activeRequestTab === 'Params' ? (
                     <>
                        <div className="px-4 py-2 text-xs font-bold text-gray-500 dark:text-gray-400 border-b border-gray-200 dark:border-white/5">Query Parameters</div>
                        <div className="flex border-b border-gray-200 dark:border-white/5 text-[10px] text-gray-500 font-medium bg-gray-50 dark:bg-[#161616]">
                           <div className="flex-1 px-4 py-1 border-r border-gray-200 dark:border-white/5">PARAMETER</div>
                           <div className="flex-1 px-4 py-1">VALUE</div>
                        </div>
                        
                        {/* Empty State for Params */}
                        <div className="flex-1 flex flex-col items-center justify-center text-gray-600 bg-white dark:bg-[#131313]">
                           <div className="w-12 h-12 border-2 border-gray-700/50 rounded-lg mb-3 flex items-center justify-center opacity-50">
                              <Hash className="w-6 h-6" />
                           </div>
                           <div className="text-xs mb-1 text-gray-500">No query parameters added</div>
                           <div className="text-[10px] opacity-40">Click the button below to add your first query parameter</div>
                        </div>

                        <div className="p-2 border-t border-gray-200 dark:border-white/5 bg-gray-50 dark:bg-[#161616]">
                           <button className="text-[10px] flex items-center text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white bg-white dark:bg-[#252525] px-2 py-1 rounded border border-gray-200 dark:border-white/5 transition-colors">
                              <Plus className="w-3 h-3 mr-1" /> Add Parameter
                           </button>
                        </div>
                     </>
                  ) : (
                     <div className="flex-1 flex items-center justify-center text-gray-600">
                        <div className="text-xs">{activeRequestTab} Configuration</div>
                     </div>
                  )}
               </div>

               {/* Response Area */}
               <div className="flex-1 bg-white dark:bg-[#131313] flex flex-col relative min-h-0">
                  {/* Resizer Handle visual */}
                  
                  {!responseVisible || isLoading || !responseBody ? (
                     <div className="flex-1 flex flex-col items-center justify-center text-gray-500">
                        {isLoading ? (
                           <div className="flex flex-col items-center animate-in fade-in zoom-in-95 duration-300">
                              <div className="w-8 h-8 border-4 border-orange-500/30 border-t-orange-500 rounded-full animate-spin mb-4"></div>
                              <div className="text-xs font-medium text-gray-400">Sending Request...</div>
                           </div>
                        ) : (
                           <>
                              <div className="w-16 h-16 border-2 border-gray-700/30 rounded-xl mb-4 flex items-center justify-center opacity-40">
                                 <Download className="w-8 h-8" />
                              </div>
                              <div className="text-sm font-medium text-gray-300 mb-1">No Response Yet</div>
                              <div className="text-xs opacity-60">Send a request to see the response here</div>
                           </>
                        )}
                     </div>
                  ) : (
                     <div className="flex-1 flex flex-col min-h-0 animate-in slide-in-from-bottom-4 duration-500">
                        {/* Response Header */}
                        <div className="h-10 border-b border-gray-200 dark:border-white/5 flex items-center px-3 justify-between bg-white dark:bg-[#131313]">
                           <div className="flex items-center space-x-4">
                              {responseStatus !== null && (
                                <span className={`${responseStatus >= 200 && responseStatus < 300 ? 'bg-green-600' : 'bg-red-600'} text-white text-[10px] font-bold px-1.5 py-0.5 rounded shadow-sm`}>
                                  {responseStatus} {responseStatus === 200 ? 'OK' : ''}
                                </span>
                              )}
                              <span className="text-xs text-gray-400 flex items-center"><Clock className="w-3 h-3 mr-1"/> {responseTime || '-'}</span>
                              <span className="text-xs text-gray-400 flex items-center"><Database className="w-3 h-3 mr-1"/> {responseSize || '-'}</span>
                           </div>
                           <div className="flex items-center space-x-2 text-gray-500">
                              <AlignLeft className="w-3.5 h-3.5 hover:text-white cursor-pointer transition-colors" />
                              <Copy className="w-3.5 h-3.5 hover:text-white cursor-pointer transition-colors" />
                              <Save className="w-3.5 h-3.5 hover:text-white cursor-pointer transition-colors" />
                              <Download className="w-3.5 h-3.5 hover:text-white cursor-pointer transition-colors" />
                              <Trash2 className="w-3.5 h-3.5 hover:text-white cursor-pointer transition-colors" />
                           </div>
                        </div>

                        {/* Response Tabs */}
                        <div className="h-8 border-b border-gray-200 dark:border-white/5 flex items-center px-3 space-x-6 text-[11px] text-gray-500 bg-gray-50 dark:bg-[#161616]">
                           {['Body', 'Headers', 'Cookies', 'Timing', 'Raw'].map((tab) => (
                              <div 
                                 key={tab}
                                 onClick={() => setActiveResponseTab(tab)}
                                 className={`h-full flex items-center cursor-pointer border-b-2 transition-colors ${
                                    activeResponseTab === tab 
                                    ? 'text-orange-500 border-orange-500' 
                                    : 'border-transparent hover:text-gray-900 dark:hover:text-gray-300'
                                 }`}
                              >
                                 {tab}
                              </div>
                           ))}
                        </div>

                        {/* Response Content (JSON) */}
                        <div className="flex-1 overflow-auto p-0 bg-gray-50 dark:bg-[#0D0D0D] font-mono text-[11px] leading-relaxed custom-scrollbar">
                           <div className="flex border-b border-gray-200 dark:border-white/5 sticky top-0 bg-gray-50 dark:bg-[#0D0D0D] z-10">
                              <div className="px-3 py-1 text-gray-500 dark:text-gray-400 flex items-center cursor-pointer hover:bg-gray-200 dark:hover:bg-white/5">
                                 <Braces className="w-3 h-3 mr-1" /> JSON <ChevronDown className="w-3 h-3 ml-1" />
                              </div>
                              <div className="flex-1"></div>
                              <div className="px-3 py-1 text-gray-500 hover:text-gray-900 dark:hover:text-gray-300 cursor-pointer flex items-center">
                                 <Search className="w-3 h-3 mr-1.5" /> Search in response...
                              </div>
                           </div>
                           
                           <div className="p-4">
                              {activeResponseTab === 'Body' ? (
                                 <div className="flex">
                                    {/* Line Numbers */}
                                    <div className="flex flex-col text-gray-400 dark:text-gray-700 select-none pr-4 text-right border-r border-gray-200 dark:border-white/5 mr-4 min-w-[20px]">
                                       {responseBody && JSON.stringify(responseBody, null, 2).split('\n').map((_, i) => <div key={i}>{i+1}</div>)}
                                    </div>
                                    {/* Code */}
                                    <div className="text-gray-800 dark:text-gray-300 whitespace-pre font-mono">
                                       <SyntaxHighlightedJson data={responseBody} />
                                    </div>
                                 </div>
                              ) : (
                                 <div className="text-gray-500 p-4">
                                    {/* Placeholder for other tabs */}
                                    Content for {activeResponseTab}
                                 </div>
                              )}
                           </div>
                        </div>
                     </div>
                  )}
               </div>
            </div>
          </div>
        </div>

        {/* Performance Footer */}
        <div className="h-6 bg-gray-100 dark:bg-[#0A0A0A] border-t border-gray-200 dark:border-black flex items-center px-3 text-[10px] font-mono text-gray-500 space-x-4 select-none">
          <div className="flex items-center space-x-1.5">
             <Activity className="w-3 h-3 text-blue-500" />
             <span className="text-gray-400">RAM:</span>
             <span className="text-blue-400 transition-all duration-500">{memoryUsage} MB</span>
          </div>
          <div className="w-px h-3 bg-white/10"></div>
          <div className="flex items-center space-x-1.5">
             <Cpu className="w-3 h-3 text-purple-500" />
             <span className="text-gray-400">CPU:</span>
             <span className="text-purple-400 transition-all duration-500">{cpuUsage}%</span>
          </div>
          <div className="w-px h-3 bg-white/10"></div>
          <div className="flex items-center space-x-1.5">
             <Layers className="w-3 h-3 text-green-500" />
             <span className="text-gray-400">Threads:</span>
             <span className="text-green-400">{threads}</span>
          </div>
          <div className="flex-1"></div>
          <div className="flex items-center space-x-1.5 opacity-60">
             <span className="w-1.5 h-1.5 rounded-full bg-green-500 animate-pulse"></span>
             <span>Native Engine Active</span>
          </div>
        </div>
      </div>
    </div>
  );
};

// Helper Components
const SidebarItem = ({ icon, label, hasChildren, active }: { icon: React.ReactNode, label: string, hasChildren?: boolean, active?: boolean }) => (
   <div className={`flex items-center px-2 py-1 rounded cursor-pointer group transition-colors ${active ? 'bg-gray-200 dark:bg-white/10 text-gray-900 dark:text-white' : 'hover:bg-gray-200 dark:hover:bg-white/5 text-gray-500 dark:text-gray-400 hover:text-gray-900 dark:hover:text-gray-200'}`}>
      <div className="mr-1.5 opacity-70">{icon}</div>
      <span className="truncate text-xs">{label}</span>
      <div className="flex-1"></div>
      {hasChildren && (
         <ChevronRight className="w-3 h-3 opacity-0 group-hover:opacity-50 transition-opacity" />
      )}
   </div>
);

const HistoryEntry = ({ method, url, status, time, duration, active, onClick }: { method: string, url: string, status: string, time: string, duration: string, active?: boolean, onClick?: () => void }) => (
   <div 
      onClick={onClick}
      className={`group px-4 py-2 cursor-pointer border-l-2 transition-colors ${active ? 'bg-gray-100 dark:bg-[#202020] border-orange-500' : 'hover:bg-gray-100 dark:hover:bg-[#202020] border-transparent bg-gray-50 dark:bg-[#1a1a1a]'}`}
   >
      <div className="flex justify-between items-center mb-1">
         <div className="flex items-center space-x-2">
            <span className={`text-[9px] font-bold px-1 rounded ${method === 'GET' ? 'bg-blue-600 text-white' : method === 'POST' ? 'bg-orange-600 text-white' : 'bg-gray-600 text-white'}`}>{method}</span>
            <span className="text-gray-600 dark:text-gray-300 text-xs">New Request</span>
         </div>
         <span className={`text-[9px] px-1 rounded ${status === '200' ? 'bg-green-500/10 text-green-400' : 'bg-orange-500/10 text-orange-400'}`}>{status}</span>
      </div>
      <div className="text-[10px] text-gray-500 font-mono truncate mb-1 opacity-70">{url !== 'New Request' ? url : 'https://jsonplaceholder.typicode.com...'}</div>
      <div className="flex justify-between items-center text-[9px] text-gray-600">
         <span className="flex items-center"><Clock className="w-2.5 h-2.5 mr-1"/> {time}</span>
         <span className="flex items-center"><Zap className="w-2.5 h-2.5 mr-1"/> {duration}</span>
         <div className="hidden group-hover:flex space-x-2 text-gray-400 animate-in fade-in">
            <Copy className="w-2.5 h-2.5 hover:text-gray-900 dark:hover:text-white" />
            <Trash2 className="w-2.5 h-2.5 hover:text-gray-900 dark:hover:text-white" />
         </div>
      </div>
   </div>
);

export default AppPreview;

const SyntaxHighlightedJson = ({ data }: { data: any }) => {
  if (data === null) return <span className="text-gray-500 italic">null</span>;
  if (data === undefined) return <span className="text-gray-500 italic">undefined</span>;
  if (typeof data === 'boolean') return <span className="text-rose-400 font-semibold">{data.toString()}</span>;
  if (typeof data === 'number') return <span className="text-orange-400">{data}</span>;
  if (typeof data === 'string') return <span className="text-emerald-400">"{data}"</span>;

  if (Array.isArray(data)) {
    if (data.length === 0) return <span className="text-gray-400">[]</span>;
    return (
      <span>
        <span className="text-gray-500 dark:text-gray-400">[</span>
        <div className="pl-6 border-l border-gray-300 dark:border-white/5 ml-1">
          {data.map((item, index) => (
            <div key={index} className="my-0.5">
              <SyntaxHighlightedJson data={item} />
              {index < data.length - 1 && <span className="text-gray-500">,</span>}
            </div>
          ))}
        </div>
        <span className="text-gray-500 dark:text-gray-400">]</span>
      </span>
    );
  }

  if (typeof data === 'object') {
    const keys = Object.keys(data);
    if (keys.length === 0) return <span className="text-gray-400">{"{}"}</span>;
    return (
      <span>
        <span className="text-gray-500 dark:text-gray-400">{'{'}</span>
        <div className="pl-6 border-l border-gray-300 dark:border-white/5 ml-1">
          {keys.map((key, index) => (
            <div key={key} className="my-0.5">
              <span className="text-blue-600 dark:text-blue-400">"{key}"</span>
              <span className="text-gray-500 dark:text-gray-400">: </span>
              <SyntaxHighlightedJson data={data[key]} />
              {index < keys.length - 1 && <span className="text-gray-500">,</span>}
            </div>
          ))}
        </div>
        <span className="text-gray-500 dark:text-gray-400">{'}'}</span>
      </span>
    );
  }

  return <span>{String(data)}</span>;
};