import React, { useState, useEffect } from 'react';
import { Clock } from 'lucide-react';

interface TimeLeft {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
}

interface CompactCountdownProps {
  timeLeft: TimeLeft;
  isLaunched: boolean;
}

export const CompactCountdown: React.FC<CompactCountdownProps> = ({ timeLeft, isLaunched }) => {
  // Format time parts that are non-zero
  const formatCountdown = () => {
    const parts = [];
    if (timeLeft.days > 0) parts.push(`${timeLeft.days}d`);
    if (timeLeft.hours > 0) parts.push(`${timeLeft.hours}h`);
    if (timeLeft.minutes > 0) parts.push(`${timeLeft.minutes}m`);
    if (parts.length === 0) parts.push(`${timeLeft.seconds}s`);
    
    return parts.join(' ');
  };

  if (isLaunched) {
    return (
      <span className="flex items-center gap-2">
        <span>Download for macOS</span>
        <span className="text-xs opacity-70">v1.0</span>
      </span>
    );
  }

  return (
    <span className="flex items-center gap-2">
      <Clock className="w-4 h-4" />
      <span>Launches in {formatCountdown()}</span>
    </span>
  );
};
