import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, useNavigate, useParams, useLocation } from 'react-router-dom';
import { LandingPage } from './components/LandingPage';
import { Docs } from './components/Docs';
import { CommandPalette } from './components/CommandPalette';

import { Sun, Moon } from 'lucide-react';

const AppContent: React.FC = () => {
  const [theme, setTheme] = useState<'light' | 'dark'>('light');
  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (theme === 'dark') {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [theme]);

  // Cmd+K / Ctrl+K listener
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault();
        setIsSearchOpen(true);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  const toggleTheme = () => {
    setTheme(prev => prev === 'dark' ? 'light' : 'dark');
  };

  const handleNavigate = (page: 'landing' | 'docs', sectionId?: string) => {
    if (page === 'landing') {
      navigate('/');
    } else if (page === 'docs') {
      if (sectionId) {
        navigate(`/docs/${sectionId}`);
      } else {
        navigate('/docs');
      }
    }
    window.scrollTo(0, 0);
  };

  return (
    <>
      <Routes>
        <Route 
          path="/" 
          element={
            <LandingPage 
              onNavigate={handleNavigate} 
              onOpenSearch={() => setIsSearchOpen(true)} 
            />
          } 
        />
        <Route 
          path="/docs" 
          element={
            <DocsWrapper 
              onNavigate={handleNavigate} 
              onOpenSearch={() => setIsSearchOpen(true)} 
            />
          } 
        />
        <Route 
          path="/docs/:section" 
          element={
            <DocsWrapper 
              onNavigate={handleNavigate} 
              onOpenSearch={() => setIsSearchOpen(true)} 
            />
          } 
        />
      </Routes>
      
      {/* Command Palette */}
      <CommandPalette 
        isOpen={isSearchOpen}
        onClose={() => setIsSearchOpen(false)}
        onNavigate={handleNavigate}
        toggleTheme={toggleTheme}
      />
      
      {/* Theme Toggle */}
      <button 
        onClick={toggleTheme}
        className="fixed bottom-6 left-6 z-50 p-4 rounded-full bg-white/10 backdrop-blur-md border border-white/10 shadow-lg hover:bg-white/20 transition-all group"
        aria-label="Toggle theme"
      >
        {theme === 'dark' ? (
          <Sun className="w-6 h-6 text-yellow-400 group-hover:rotate-90 transition-transform" />
        ) : (
          <Moon className="w-6 h-6 text-blue-600 group-hover:-rotate-12 transition-transform" />
        )}
      </button>
    </>
  );
};

// Wrapper component to handle URL params for Docs
const DocsWrapper: React.FC<{
  onNavigate: (page: 'landing' | 'docs', sectionId?: string) => void;
  onOpenSearch: () => void;
}> = ({ onNavigate, onOpenSearch }) => {
  const { section } = useParams<{ section?: string }>();
  
  return (
    <Docs 
      onNavigate={onNavigate} 
      onOpenSearch={onOpenSearch} 
      targetSection={section}
    />
  );
};

const App: React.FC = () => {
  return (
    <BrowserRouter>
      <AppContent />
    </BrowserRouter>
  );
};

export default App;