import React, { useEffect, useRef } from 'react';

export const FishBackground: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    let animationFrameId: number;
    let width = window.innerWidth;
    let height = window.innerHeight;

    // Fish state
    const fish = {
      x: width / 2,
      y: height / 2,
      vx: 0,
      vy: 0,
      angle: 0,
      targetX: width / 2,
      targetY: height / 2,
      segments: [] as { x: number; y: number }[],
      numSegments: 12,
      segmentLength: 15,
    };

    // Initialize segments
    for (let i = 0; i < fish.numSegments; i++) {
      fish.segments.push({ x: fish.x, y: fish.y });
    }

    const handleResize = () => {
      width = window.innerWidth;
      height = window.innerHeight;
      canvas.width = width;
      canvas.height = height;
    };

    const handleMouseMove = (e: MouseEvent) => {
      fish.targetX = e.clientX;
      fish.targetY = e.clientY;
    };

    const handleTouchMove = (e: TouchEvent) => {
      if (e.touches.length > 0) {
        fish.targetX = e.touches[0].clientX;
        fish.targetY = e.touches[0].clientY;
      }
    };

    window.addEventListener('resize', handleResize);
    window.addEventListener('mousemove', handleMouseMove);
    window.addEventListener('touchmove', handleTouchMove);

    handleResize();

    // Logo colors: Orange to Rose
    // #FB923C (251, 146, 60) -> #F43F5E (244, 63, 94)
    let hue = 0;

    const update = () => {
      hue += 0.5; // Cycle colors slowly
      
      // Smoothly move towards target
      const dx = fish.targetX - fish.x;
      const dy = fish.targetY - fish.y;
      const dist = Math.sqrt(dx * dx + dy * dy);
      
      // Easing
      const speed = Math.min(dist * 0.05, 8); // Max speed cap
      const angle = Math.atan2(dy, dx);
      
      fish.vx = Math.cos(angle) * speed;
      fish.vy = Math.sin(angle) * speed;
      
      fish.x += fish.vx;
      fish.y += fish.vy;
      fish.angle = angle;

      // Update segments (Inverse Kinematics-ish / Follow)
      // Head is at fish.x, fish.y
      let targetX = fish.x;
      let targetY = fish.y;

      for (let i = 0; i < fish.numSegments; i++) {
        const segment = fish.segments[i];
        const dx = targetX - segment.x;
        const dy = targetY - segment.y;
        const dist = Math.sqrt(dx * dx + dy * dy);
        const angle = Math.atan2(dy, dx);

        // Move segment to be at distance 'segmentLength' from target
        const t = Math.min(1, fish.segmentLength / dist);
        if (dist > 0) {
           segment.x = targetX - Math.cos(angle) * fish.segmentLength;
           segment.y = targetY - Math.sin(angle) * fish.segmentLength;
        }
        
        targetX = segment.x;
        targetY = segment.y;
      }
    };

    const draw = () => {
      ctx.clearRect(0, 0, width, height);
      
      // Dynamic Color Calculation
      // Base it on the logo gradient but shift it over time
      // Orange: hsl(27, 96%, 61%)
      // Rose: hsl(343, 88%, 60%)
      
      // We'll oscillate between these two primarily, but let it drift
      const time = Date.now() * 0.001;
      const baseHue = 27 + Math.sin(time) * 40; // Oscillate around orange/red
      
      ctx.save();
      
      // Aesthetic: Glowing ghost fish
      ctx.shadowBlur = 25;
      ctx.shadowColor = `hsla(${baseHue}, 90%, 60%, 0.6)`;
      ctx.fillStyle = `hsla(${baseHue}, 90%, 60%, 0.15)`;
      ctx.strokeStyle = `hsla(${baseHue}, 90%, 70%, 0.3)`;
      ctx.lineWidth = 2;

      // Draw head
      ctx.beginPath();
      ctx.arc(fish.x, fish.y, 12, 0, Math.PI * 2);
      ctx.fill();
      ctx.stroke();

      // Draw segments with varying sizes and slight color shift along body
      fish.segments.forEach((seg, i) => {
        const size = 10 * (1 - i / fish.numSegments); // Tapering tail
        const segmentHue = baseHue - (i * 2); // Shift hue along body
        
        ctx.beginPath();
        ctx.arc(seg.x, seg.y, size, 0, Math.PI * 2);
        ctx.fillStyle = `hsla(${segmentHue}, 90%, 60%, ${0.15 - i * 0.01})`;
        ctx.fill();
      });
      
      // Draw connection lines (spine)
      ctx.beginPath();
      ctx.moveTo(fish.x, fish.y);
      fish.segments.forEach(seg => ctx.lineTo(seg.x, seg.y));
      ctx.strokeStyle = `hsla(${baseHue}, 90%, 70%, 0.2)`;
      ctx.stroke();

      ctx.restore();
    };

    const loop = () => {
      update();
      draw();
      animationFrameId = requestAnimationFrame(loop);
    };

    loop();

    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('touchmove', handleTouchMove);
      cancelAnimationFrame(animationFrameId);
    };
  }, []);

  return (
    <canvas 
      ref={canvasRef} 
      className="fixed inset-0 pointer-events-none z-0 opacity-60"
    />
  );
};
