import { useState, useEffect } from 'react';

export const useLaunchCountdown = () => {
  // Set launch time to 3 PM GMT (15:00:00 GMT)
  // We use a fixed date for the launch. Let's assume it's "today" or the next occurrence if passed, 
  // but for a product launch it's usually a specific date. 
  // Given the context, I'll set it to a specific date in the near future or just handle the time component if it's a daily thing (unlikely).
  // I will assume the launch is scheduled for *tomorrow* at 3 PM GMT if today 3 PM GMT has passed, or just a fixed date.
  // However, usually "launch" implies a specific date. I will use a date far enough in the future or a specific one if known.
  // Since I don't have a specific date, I will set it to the NEXT occurrence of 3 PM GMT.
  
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });
  const [isLaunched, setIsLaunched] = useState(false);
  const [localLaunchTime, setLocalLaunchTime] = useState('');

  useEffect(() => {
    // Target: 3 PM GMT
    const now = new Date();
    const target = new Date();
    
    // Set to 15:00 GMT (3 PM)
    target.setUTCHours(15, 0, 0, 0);

    // If 3 PM GMT has already passed today, target tomorrow
    if (now.getTime() > target.getTime()) {
      target.setDate(target.getDate() + 1);
    }

    // Format local launch time for display
    setLocalLaunchTime(target.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', timeZoneName: 'short' }));

    const calculateTimeLeft = () => {
      const currentTime = new Date().getTime();
      const difference = target.getTime() - currentTime;

      if (difference > 0) {
        setTimeLeft({
          days: Math.floor(difference / (1000 * 60 * 60 * 24)),
          hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
          minutes: Math.floor((difference / 1000 / 60) % 60),
          seconds: Math.floor((difference / 1000) % 60),
        });
        setIsLaunched(false);
      } else {
        setIsLaunched(true);
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      }
    };

    calculateTimeLeft();
    const timer = setInterval(calculateTimeLeft, 1000);

    return () => clearInterval(timer);
  }, []);

  return { timeLeft, isLaunched, localLaunchTime };
};
