import { ChatMessage } from '../types';

// Cloudflare Worker endpoint
const WORKER_URL = 'https://ai.niiyeboah1996.workers.dev/';

/**
 * Sends the chat history to the Cloudflare Worker and returns the AI's response.
 * The worker has the Aether context built-in, so we only send the conversation history.
 */
export const sendMessageToGemini = async (
  messages: ChatMessage[],
  systemInstruction?: string,
  contextType?: 'general' | 'docs'
): Promise<string> => {
  try {
    // Format messages for the worker API
    const formattedMessages = messages.map((msg) => ({
      role: msg.role === 'model' ? 'assistant' : msg.role,
      content: msg.text,
    }));

    // Send request to Cloudflare Worker
    const response = await fetch(WORKER_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        messages: formattedMessages,
        system_instruction: systemInstruction,
        contextType,
      }),
    });

    if (!response.ok) {
      throw new Error(`Worker returned ${response.status}`);
    }

    const data = await response.json() as { response?: string };
    
    // Return the AI's response text
    return data.response || "I couldn't process that request right now.";
  } catch (error) {
    console.error('Worker API Error:', error);
    return "Sorry, I'm having trouble connecting to the Aether knowledge base right now. Please try again.";
  }
};