import React, { useState, useEffect, useRef } from 'react';
import { Search, FileText, Hash, Shield, Folder, Zap, Database, Globe, Play, CheckCircle, Moon, Sun, Laptop, ArrowRight, Command } from 'lucide-react';

interface CommandPaletteProps {
  isOpen: boolean;
  onClose: () => void;
  onNavigate: (page: 'landing' | 'docs', sectionId?: string) => void;
  toggleTheme: () => void;
}

type SearchItem = {
  id: string;
  title: string;
  description?: string;
  category: 'Navigation' | 'Documentation' | 'Actions';
  icon: React.ElementType;
  action: () => void;
  keywords?: string[];
};

export const CommandPalette: React.FC<CommandPaletteProps> = ({ isOpen, onClose, onNavigate, toggleTheme }) => {
  const [query, setQuery] = useState('');
  const [selectedIndex, setSelectedIndex] = useState(0);
  const inputRef = useRef<HTMLInputElement>(null);

  // Reset state when opened
  useEffect(() => {
    if (isOpen) {
      setQuery('');
      setSelectedIndex(0);
      // Small delay to ensure DOM is ready
      setTimeout(() => inputRef.current?.focus(), 50);
    }
  }, [isOpen]);

  // Close on Escape
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') onClose();
    };
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [onClose]);

  const items: SearchItem[] = [
    // Navigation
    {
      id: 'nav-home',
      title: 'Home',
      description: 'Go to the landing page',
      category: 'Navigation',
      icon: Laptop,
      action: () => onNavigate('landing'),
    },
    {
      id: 'nav-docs',
      title: 'Documentation',
      description: 'Browse the full documentation',
      category: 'Navigation',
      icon: FileText,
      action: () => onNavigate('docs'),
    },
    
    // Actions
    {
      id: 'action-theme',
      title: 'Toggle Theme',
      description: 'Switch between light and dark mode',
      category: 'Actions',
      icon: Moon, // Dynamic icon would be better but this works for list
      action: toggleTheme,
      keywords: ['dark', 'light', 'mode'],
    },

    // Documentation Sections
    {
      id: 'doc-tutorial',
      title: 'Tutorial: First Collection',
      category: 'Documentation',
      icon: Play,
      action: () => onNavigate('docs', 'tutorial'),
      keywords: ['start', 'begin', 'guide'],
    },
    {
      id: 'doc-concepts',
      title: 'Core Concepts',
      category: 'Documentation',
      icon: Database,
      action: () => onNavigate('docs', 'concepts'),
      keywords: ['philosophy', 'yaml', 'structure'],
    },
    {
      id: 'doc-structure',
      title: 'File Structure',
      category: 'Documentation',
      icon: Folder,
      action: () => onNavigate('docs', 'structure'),
      keywords: ['schema', 'layout'],
    },
    {
      id: 'doc-auth',
      title: 'Authentication',
      category: 'Documentation',
      icon: Shield,
      action: () => onNavigate('docs', 'authentication'),
      keywords: ['security', 'token', 'bearer', 'api key'],
    },
    {
      id: 'doc-requests',
      title: 'Requests',
      category: 'Documentation',
      icon: Zap,
      action: () => onNavigate('docs', 'requests'),
      keywords: ['http', 'graphql', 'grpc', 'websocket'],
    },
    {
      id: 'doc-workflows',
      title: 'Workflows',
      category: 'Documentation',
      icon: ArrowRight,
      action: () => onNavigate('docs', 'workflows'),
      keywords: ['chain', 'automation', 'steps'],
    },
    {
      id: 'doc-tests',
      title: 'Tests',
      category: 'Documentation',
      icon: CheckCircle,
      action: () => onNavigate('docs', 'tests'),
      keywords: ['assertions', 'scripts', 'validation'],
    },
  ];

  // Filter items
  const filteredItems = items.filter((item) => {
    const searchContent = `${item.title} ${item.description || ''} ${item.category} ${item.keywords?.join(' ') || ''}`.toLowerCase();
    return searchContent.includes(query.toLowerCase());
  });

  // Handle keyboard navigation
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!isOpen) return;

      switch (e.key) {
        case 'ArrowDown':
          e.preventDefault();
          setSelectedIndex((prev) => (prev + 1) % filteredItems.length);
          break;
        case 'ArrowUp':
          e.preventDefault();
          setSelectedIndex((prev) => (prev - 1 + filteredItems.length) % filteredItems.length);
          break;
        case 'Enter':
          e.preventDefault();
          if (filteredItems[selectedIndex]) {
            filteredItems[selectedIndex].action();
            onClose();
          }
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, filteredItems, selectedIndex, onClose]);

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-[100] flex items-start justify-center pt-[20vh] px-4">
      {/* Backdrop */}
      <div 
        className="absolute inset-0 bg-black/20 dark:bg-black/60 backdrop-blur-sm transition-opacity" 
        onClick={onClose}
      />

      {/* Modal */}
      <div className="relative w-full max-w-2xl bg-white dark:bg-[#111] rounded-2xl shadow-2xl border border-gray-200 dark:border-white/10 overflow-hidden flex flex-col max-h-[60vh] animate-in fade-in zoom-in-95 duration-200">
        
        {/* Search Input */}
        <div className="flex items-center px-4 py-4 border-b border-gray-200 dark:border-white/5">
          <Search className="w-5 h-5 text-gray-400 mr-3" />
          <input
            ref={inputRef}
            type="text"
            placeholder="Search documentation, actions, and more..."
            className="flex-1 bg-transparent text-lg text-gray-900 dark:text-white placeholder-gray-400 focus:outline-none"
            value={query}
            onChange={(e) => {
              setQuery(e.target.value);
              setSelectedIndex(0);
            }}
          />
          <div className="hidden md:flex items-center gap-1">
            <kbd className="bg-gray-100 dark:bg-[#222] text-xs text-gray-500 px-2 py-1 rounded border border-gray-200 dark:border-white/10">ESC</kbd>
          </div>
        </div>

        {/* Results */}
        <div className="overflow-y-auto flex-1 p-2">
          {filteredItems.length === 0 ? (
            <div className="py-12 text-center text-gray-500">
              <p>No results found for "{query}"</p>
            </div>
          ) : (
            <div className="space-y-1">
              {filteredItems.map((item, index) => (
                <button
                  key={item.id}
                  onClick={() => {
                    item.action();
                    onClose();
                  }}
                  className={`w-full flex items-center gap-4 px-4 py-3 rounded-xl text-left transition-colors ${
                    index === selectedIndex
                      ? 'bg-blue-500 text-white'
                      : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-white/5'
                  }`}
                  onMouseEnter={() => setSelectedIndex(index)}
                >
                  <div className={`p-2 rounded-lg ${
                    index === selectedIndex ? 'bg-white/20' : 'bg-gray-100 dark:bg-[#222]'
                  }`}>
                    <item.icon className="w-5 h-5" />
                  </div>
                  <div className="flex-1">
                    <div className="font-medium">{item.title}</div>
                    {item.description && (
                      <div className={`text-sm ${
                        index === selectedIndex ? 'text-white/80' : 'text-gray-500'
                      }`}>
                        {item.description}
                      </div>
                    )}
                  </div>
                  {item.category === 'Documentation' && (
                    <div className={`text-xs px-2 py-1 rounded ${
                      index === selectedIndex ? 'bg-white/20 text-white' : 'bg-gray-100 dark:bg-[#222] text-gray-500'
                    }`}>
                      Docs
                    </div>
                  )}
                </button>
              ))}
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="px-4 py-3 bg-gray-50 dark:bg-[#1a1a1a] border-t border-gray-200 dark:border-white/5 flex items-center justify-between text-xs text-gray-500">
          <div className="flex gap-4">
            <span className="flex items-center gap-1">
              <Command className="w-3 h-3" />
              <span className="font-medium">Select</span>
            </span>
            <span className="flex items-center gap-1">
              <span className="font-medium"></span>
              <span>Navigate</span>
            </span>
          </div>
          <div>
            Aether Command
          </div>
        </div>
      </div>
    </div>
  );
};
